import React from 'react';
import './../App.css';
import AzeemPic from './../assets/images/azeem.jpg';
import ReactRotatingText from 'react-rotating-text';
import { Link } from 'react-scroll';

const RotatingContent = ["Azeem Ahmad", "a Web Designer", "UI/UX Designer", "a Front-End Web Developer", "a React JS Developer", "a Freelancer"];


function Home() {
  return (
    <section className="intro">

        <div className="container">
            <div className="row align-items-center">
                <div className="col-md-7 text-left intro-content">
                    

                <div className="main-text" id="selector">
                        <h3>Hi there !</h3>
                        <h1 className="rotating-text-heading">
					I'm
						<span className="">
                        <ReactRotatingText
                            items={RotatingContent}
                            emptyPause={250}
                            pause={2000}
                            typingInterval={70}
                        />
						</span>
					</h1>
                        <p>I'm a UI/UX Designer and Front-End Web Developer based in Mumbai, India. I strives to build immersive and beautiful web applications through carefully crafted code and user-centric design.</p>
                        <div className="call-to-actions-home">
                            <div className="text-left">
                                <a href="#0" className="btn btn-services mr-3"><span>Download CV</span></a>
                                <Link to="aboutme" spy={true} smooth={true} duration={500} className="btn btn-services-invert"><span>More About Me</span></Link>
                            </div>
                        </div>
                    </div>


                </div>
                <div className="col-md-5 intro-img text-right">
                    <img src={AzeemPic} alt="azeem" />
                </div>

            </div>
        </div>


    </section>
  );
}

export default Home;
