import React from 'react';
import './../App.css';
import CountUp from 'react-countup';

function Counter() {
  return (
    <section className="section counter-section">
        <div className="container">
            <div className="row justify-content-center" id="counter">
                <div className="col-md-3 text-white text-center pt-3 pb-3">
                    <div className="counter-icon">
                        <i className="fa fa-diamond" aria-hidden="true"></i>
                    </div>
                    <h1 className="counter-value">
                        <CountUp end={4} duration={5} />
                        <sup>+</sup>
                    </h1>
                    <h5 className="counter-name">Years Working Experience</h5>
                </div>

                <div className="col-md-3 text-white text-center pt-3 pb-3">
                    <div className="counter-icon">
                        <i className="fa fa-briefcase" aria-hidden="true"></i>
                    </div>
                    <h1 className="counter-value">
                        <CountUp end={100} duration={5} />
                        <sup>+</sup>
                    </h1>
                    <h5 className="counter-name">Completed Projects</h5>
                </div>

                <div className="col-md-3 text-white text-center pt-3 pb-3">
                    <div className="counter-icon">
                        <i className="fa fa-smile-o" aria-hidden="true"></i>
                    </div>
                    <h1 className="counter-value">
                        <CountUp end={95} duration={5} />
                        <sup>+</sup>
                    </h1>
                    <h5 className="counter-name">No. of Happy Clients</h5>
                </div>
                <div className="col-md-3 text-white text-center pt-3 pb-3"> 
                    <div className="counter-icon">
                        <i className="fa fa-coffee" aria-hidden="true"></i>
                    </div>                       
                    <h1 className="counter-value">
                        <CountUp end={1000} duration={5} />
                        <sup>+</sup>
                    </h1>
                    <h5 className="counter-name">Coffee</h5>
                </div>

            </div>
        </div>
    </section>
  );
}

export default Counter;