import React from 'react';
import './../App.css';
import ContactImg from './../assets/images/contact-1.png';

const SectionMrg = {
  marginBottom: 0
}

const ContactPage = "contact-page";


class Contact extends React.Component {

  componentDidMount() {
    document.body.classList.add(ContactPage);
  }

  componentWillUnmount() {
    document.body.classList.remove(ContactPage);
  }



  render() {
    return (
      <section className="contact-section" style={SectionMrg}> 
        <div className="container pt-5">
            <div className="contact-content">
                <h1 className="mb-4">Contact Us</h1>
                <p>Contact us and i'll get back to you as soon as possible with a solution to whatever issues you're having with your website.</p>
            </div>
        </div>

        <div className="container">
            <div className="row row-30 align-items-lg-center justify-content-center">
              <div className="col-lg-6 text-center">
                <div className="animate-box">
                  <div className="mt-3"><img src={ContactImg} alt="" width="410" height="auto" />
                  </div>
                </div>
              </div>
              <div className="col-lg-6">
                <div className="box-contact-form wow fadeIn">
                  <h2 className="mb-4">Get in touch</h2>
                  <form className="rd-form rd-mailform">
                    <div className="row row-20">
                      <div className="col-12">
                        <div className="form-group">
                          <input className="form-input form-control" id="contact-name" type="text" name="name" placeholder="Your Name" />
                        </div>
                      </div>
                      <div className="col-12">
                        <div className="form-group">
                          <input className="form-input form-control" id="email" type="email" name="email" placeholder="Your Email Address" />
                        </div>
                      </div>
                      <div className="col-12">
                        <div className="form-group">
                          <input className="form-input form-control" id="contact-phone" type="text" name="phone" placeholder="Your Phone No." />
                        </div>
                      </div>
                      <div className="col-12">
                        <div className="form-group">
                          <textarea className="form-input form-control form-control-last-child" id="contact-message" name="message" rows="4" placeholder="Your Message"></textarea>
                        </div>
                      </div>
                      <div className="col-12">
                        <a href="#0" className="btn btn-sm btn-primary">Send</a>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>


          <div className="map mt-5">
            <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d12831.57296227023!2d72.8737538508669!3d19.062324193820334!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3be7c89a5efa11d5%3A0x90728ea7729a78f9!2sKurla%2C%20Mumbai%2C%20Maharashtra!5e0!3m2!1sen!2sin!4v1597262423745!5m2!1sen!2sin" width="100%" height="450" frameBorder="0" allowFullScreen="" aria-hidden="false" tabIndex="0" title="map"></iframe>
          </div>


      </section>
    );
  }
}

export default Contact;
