import React from 'react';
import { NavLink } from 'react-router-dom';

const BrandName = "<Azeem />"


// const { location } = this.props;

// const homeClass = location.pathname === "/" ? "navbar-brand active" : "navbar-brand";

// const styles = {
//     headerColor : {
//         background: '#333',
//     },
// };

function Header() {
  return (
    <header>
        {/* <nav className={`navbar navbar-expand-lg navbar-dark bg-primary1 ${styles.header}`}> */}
        <nav className="navbar navbar-expand-lg navbar-dark bg-primary1">
            <div className="container">
            <NavLink to="/portfolio-old" className="navbar-brand" >
                {BrandName}
            </NavLink>
            <button
                className="navbar-toggler"
                type="button"
                data-toggle="collapse"
                data-target="#navbarNavDropdown"
                aria-controls="navbarNavDropdown"
                aria-expanded="false"
                aria-label="Toggle navigation"
            >
                <span className="navbar-toggler-icon" />
            </button>
            <div className="collapse navbar-collapse" id="navbarNavDropdown">
                <ul className="navbar-nav">
                <li className="nav-item">
                    <NavLink to="/home" activeClassName="active" className="nav-link">
                    Home
                    </NavLink>
                </li>
                <li className="nav-item">
                    <NavLink to="/about" activeClassName="active" className="nav-link">
                    About
                    </NavLink>
                </li>
                <li className="nav-item">
                    <NavLink to="/blogs" activeClassName="active" className="nav-link">
                    Blogs
                    </NavLink>
                </li>
                
                {/* <li className="nav-item dropdown">
                    <a
                    className="nav-link dropdown-toggle"
                    href="#0"
                    id="navbarDropdownMenuLink"
                    role="button"
                    data-toggle="dropdown"
                    aria-haspopup="true"
                    aria-expanded="false"
                    >
                    Services
                    </a>
                    <div className="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
                    <a className="dropdown-item" href="#0">
                    Services 1                   </a>
                    <a className="dropdown-item" href="#-">
                    Services 2
                    </a>
                    <a className="dropdown-item" href="#0">
                    Services 3
                    </a>
                    </div>
                </li> */}
                <li className="nav-item">
                    <NavLink to="/contact" activeClassName="active" className="nav-link">
                    Contact
                    </NavLink>
                </li>
                </ul>
            </div>
            </div>
        </nav>


    </header>
  );
}

export default Header;
