import React from 'react';
import BlogCards from './../Components/BlogCards';

class Blogs extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            LoadMoreText : ''
        }

        this.LoadMore = this.LoadMore.bind(this);

    }
    
    LoadMore() {
        this.setState({
            LoadMoreText : 'No Blogs Found'
        });
    }



    render() {

        const BlogArr = [
            {
                id: 1,
                title: 'Blog 1',
                description: 'Lorem ipsum for Blog 1'
            },
            {
                id: 2,
                title: 'Blog 2',
                description: 'Lorem ipsum for Blog 2'
            },
            {
                id: 3,
                title: 'Blog 3',
                description: 'Lorem ipsum for Blog 3'
            }

        ]


        const BlogData = BlogArr.map((item) => {
            return(
                <BlogCards key={item.id} title={item.title} description={item.description} />
            )
        }
        );


        return(
            <section className="blog-section">
                <div className="container pt-5">
                    <h1 className="mb-4 ">All Blogs</h1>
                    <div className="row">
                        {BlogData}
                    </div>
                    <button className="btn btn-services btn-sm mt-4" onClick={this.LoadMore}>Load More</button>
                    <p className="mt-3">{this.state.LoadMoreText}</p>
                </div>
            </section>
        )
    }

}


export default Blogs;