import React from 'react';
import './../App.css';
import { Element } from 'react-scroll';


function About() {
  return (
    <Element name="aboutme">
    <section className="about-section" id="about-section"> 
      <div className="container pt-5">
          <div className="about-content">
              <h1 className="mb-4">Who am I?</h1>
              <p>Hi, I'm Azeem! I'm a UI/UX Designer and Front-End Web Developer with having 4+ years of Experience.</p>
              <p>I'm a Freelance UI/UX Designer and Developer based in Mumbai, India. I strives to build immersive and beautiful web applications through carefully crafted code and user-centric design.</p>
          </div>
      </div>
    </section>
    </Element>
  );
}

export default About;
