import React from 'react';
import './../App.css';

const BrandName = "<Azeem />"


function Footer() {
  return (
    <footer>
        <div className="bg-sec-color">
            <div className="container">
                <div className="row">
                    <div className="col-md-6 text-left">
                        <p>Made with <i className="fa fa-heart"></i> By {BrandName}</p>
                    </div>
                    <div className="col-md-6 text-right">
                        <p>Copyright © 2020 | All Rights Reserved</p>
                    </div>
                </div>
            </div>
        </div>

    </footer>
  );
}

export default Footer;
